<template>
    <div id="ranklist">
        <loadings ref="load"></loadings>
        <!--<navbar></navbar>-->
        <navbar :isActive="page_belong"></navbar>
        <div class="container mundb-standard-container">
            <paper-card>
                <div>
                    <div class="table-responsive">
                        <table class="table rank-table">
                            <thead>
                            <tr>
                                <th scope="col" style="text-align: left;">Rank</th>
                                <th scope="col">Honor</th>
                                <th scope="col">Solved</th>
                                <th scope="col">AC Rate</th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr style="background-color: white;">
                                <th scope="row">
                                    <div><a href="./userinfo.php?user=ACM_ningmeng"><img
                                            class="cm-avatar cm-colorful-text"
                                            src="https://oj.ctguacm.work/image/default.png"></a>
                                        <div><strong class="cm-colorful-text">#1&nbsp;&nbsp;<small>ID：ACM_ningmeng
                                        </small>
                                        </strong>
                                            <p class="cm-colorful-text">ACM_柠檬</p></div>
                                    </div>
                                </th>
                                <td><span class="cm-colorful-text">Fleet Admiral</span></td>
                                <td>
                                    <div class="center"><a class="cm-colorful-text"
                                                           href="status.php?user_id=ACM_ningmeng">65</a></div>
                                </td>
                                <td><span class="cm-colorful-text">43.33%</span></td>
                            </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </paper-card>
        </div>
        <footers></footers>

    </div>
</template>

<script>
    import navBar from "@/components/navbar/navbar";
    import Footers from "@/components/footer/footer";
    import Loadings from "../../components/loading/loading";
    import fun from '@/assets/js/OJFunction'

    export default {
        components: {
            Loadings,
            Footers,
            'navbar': navBar
        },
        name: "ranklist",
        data() {
            return {
                page_belong: 'ranklist',
                ranklist: '',
                page_setting: {
                    access_token: "",
                    refresh_token: "",
                    language: "",
                },
            }
        },
        created() {
            this.page_setting.access_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token");
            this.page_setting.refresh_token = fun.getCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token");
            console.log(this.page_setting.access_token);
            var lang = localStorage.getItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang");
            if (lang != null) {
                this.page_setting.language = lang;
            }
            this.$http.post(this.GLOBAL.OJ_DEFAULT_BACK_SERVE + '/problemset',
                this.page_setting,
                {emulateJSON: true}).then(function (res) {

                var error_type = res.body.OJ_ERROR_TYPE;
                if (error_type == this.GLOBAL.OJ_ERROR_NO_ERROR) {
                    var remember_me = res.body.OJ_USER_INFO.saved;
                    var access_token = res.body.OJ_USER_INFO.access_token;
                    var refresh_token = res.body.OJ_USER_INFO.refresh_token;
                    if (res.body.OJ_USER_INFO.permissions.admin || res.body.OJ_USER_INFO.permissions.superAdmin) {
                        fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_ADMIN);
                    } else {
                        fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_USER);
                    }
                    if (remember_me) {
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token, 7);
                        fun.setCookieWithTime(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token, 7);
                    } else {
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "access_token", access_token);
                        fun.setCookie(this.GLOBAL.OJ_DEFAULT_NAME + "refresh_token", refresh_token);
                    }
                } else if (error_type == this.GLOBAL.OJ_ERROR_NOT_LOGIN) {
                    fun.init_account_controller(this.GLOBAL.OJ_GROUP_NORMAL_VISITOR);
                }

                lang = res.body.OJ_LANGUAGE_TYPE;
                localStorage.setItem(this.GLOBAL.OJ_DEFAULT_NAME + "lang", lang);
                this.$i18n.locale = lang;
            }, function () {
                console.log('请求失败处理');
            });
        }
    }
</script>

<style scoped lang="scss">
    #ranklist {
